const getRanges = (inputs: any[]) => {
    let sigs = inputs.filter((input) => input.id.startsWith("sig_"));
    let trigs = inputs.filter((input) => input.id.startsWith("trig_"));

    const sigs_min = Math.min(...sigs.map((sig) => sig.min));
    const sigs_max = Math.max(...sigs.map((sig) => sig.max));
    const trigs_min = Math.min(...trigs.map((trig) => trig.min));
    const trigs_max = Math.max(...trigs.map((trig) => trig.max));


    

    return [
        {
            min: sigs_min,
            max: sigs_max,
        },
        {
            min: trigs_min,
            max: trigs_max,
        },
    ];
};

