export default function GBMSearch() {
    const theme = useTheme();

    const [selectedFields, setSelectedFields] = useState<any[]>([]);
    const [open, setOpen] = useState(false);

    const methods = ["Bayesian", "Poisson", "SNR"];
    const modes = ["Mode 1", "Mode 2", "Mode 3", "Mode 4"];

    const onMethodModeSelect = (id: string, value: string[]) => {
        console.log(id, value);
    };

    const onFieldSelect = (field_ids: string[]) => {
        const selected_ = inputs.filter((input) => {
            if (field_ids.includes(input.id)) return input.id;
        });

        console.log(selected_);

        setSelectedFields(selected_);
    };

    const updateRanges = (ranges: any) => {
        inputs = inputs.map((input, index) => {
            return {
                ...input,
                min: ranges.mins[index],
                max: ranges.maxes[index],
            };
        });

        const update = getRanges(inputs);

        inputs[45].min = update[1].min;
        inputs[45].max = update[1].max;
        inputs[46].min = update[0].min;
        inputs[46].max = update[0].max;
        
        
        console.log(inputs);
        



    };

    const onSubmit = () => {
        setOpen(true);
        setTimeout(() => {
            setOpen(false);
            alert("Not yet...");
        }, 2000);
    };

    useEffect(() => {
        fetch("http://magnetars.sabanciuniv.edu/mysql_utils/get_ranges.php", {
            method: "GET",
        })
            .then((resp) => resp.json())
            .then((data) => updateRanges(data))
            .catch((reason) => console.log(reason));
    }, []);

    return (
        <HeaderFooter>
            <Typography style={theme.h1}>GBM Database Search</Typography>
            <Divider style={theme.divider} />

            <Box m={4}>
                <Stack direction={"column"} spacing={2}>
                    <Stack direction={"row"} spacing={2}>
                        <MultipleSelect
                            id="method-select"
                            label={"Methods"}
                            contents={methods}
                            onChange={onMethodModeSelect}></MultipleSelect>
                        <MultipleSelect
                            id="mode-select"
                            label={"Modes"}
                            contents={modes}
                            onChange={onMethodModeSelect}></MultipleSelect>
                    </Stack>

                    <FieldSelect
                        fields={inputs.map((input) => ({
                            id: input.id,
                            label: input.label,
                        }))}
                        onSelect={onFieldSelect}
                    />

                    <Stack>
                        <TableContainer>
                            <Table size="small">
                                <TableHead>
                                    {selectedFields.length ? (
                                        <SearchRowHead></SearchRowHead>
                                    ) : (
                                        <TableRow></TableRow>
                                    )}
                                </TableHead>
                                <TableBody>
                                    {selectedFields.map((input) => (
                                        <SearchRow
                                            key={input.id}
                                            id={input.id}
                                            label={input.label}
                                            onChange={undefined}
                                            onDelete={undefined}
                                            min={input.min}
                                            max={input.max}
                                        />
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Stack>

                    <Stack direction={"row"} justifyContent="center">
                        <Button variant="contained" onClick={onSubmit}>
                            Search
                        </Button>
                    </Stack>
                </Stack>
            </Box>

            <Backdrop
                sx={{
                    color: "#fff",
                    zIndex: (theme) => theme.zIndex.drawer + 1,
                }}
                open={open}
                onClick={undefined}>
                <CircularProgress color="inherit" />
            </Backdrop>
        </HeaderFooter>
    );
}
